@media only screen and (max-width: 699px) {
  .navbar--primary {
    .nav-links {
      position: relative;
      margin-top: 2rem;

      display: flex;
      flex-direction: column;
      gap: 0.5rem;

      .nav-link {
        &-btn {
          width: 100%;
          position: relative;
          display: flex;
          flex-direction: row;
          align-items: center;
          padding: 1rem 0 1rem 4.75rem;

          &::after {
            content: url('../assets/images/icon/navbar/navigate_next.svg');
            background-image: url('../assets/images/icon/navbar/navigate_next-blue.svg');
            width: 1.25rem;
            height: 1.25rem;
            position: absolute;
            right: 0;
          }
          &:hover,
          .active {
            color: $__primary_light;
            box-shadow: inset 0px 10px 2px -10px $__primary_light,
              inset 0px -10px 2px -10px $__primary_light;
            &::after {
              content: '';
            }
            &::before {
              content: '';
            }
          }
          &.active {
            background-color: $__primary_opacity15;
          }

          cursor: pointer;
        }
      }
    }
  }
}

@media only screen and (min-width: 700px) {
  .navbar--primary {
    margin-left: 5rem;

    .nav-links {
      display: flex;
      flex-direction: row;
      align-items: center;
      gap: 0.3rem;

      .nav-link {
        font-size: 0.9rem;

        &-btn {
          width: 100%;
          position: relative;
          display: flex;
          flex-direction: row;
          align-items: center;
          padding-right: 1.25rem;

          &::before {
            display: none;
          }
          /** display arrow **/
          &::after {
            content: url('../assets/images/icon/navbar/expand_more.svg');
            background-image: url('../assets/images/icon/navbar/expand_more-blue.svg');
            position: absolute;
            top: 0.175rem;
            right: 0;
            width: 1.25rem;
            height: 1.25rem;
            transition: $__transitionTransform;
          }
          &:hover,
          &:focus,
          &.active {
            color: $__primary_light;
            &::after {
              content: '';
            }
          }
          /** rotate arrow **/
          &.active {
            &::after {
              transform: rotate(180deg);
              transition: $__transitionTransform;
              top: -0.0075rem;
            }
          }
        }
      }
    }
  }
}

/** NAV LINKS ICONS MOBILE ::before **/
.nav-link-btn::before {
  position: absolute;
  left: 1.5rem;
  width: 1.75rem;
  height: 1.75rem;
}

.nav-link-btn {
  &.btn-games {
